.services {
  width: 100%;
  height: 80vh;
  margin-top: 9rem;
  position: relative; }
.services__realizations {
  width: 34vw;
  height: 19vw;
  padding: 2rem;
  background-color: rgba(255, 242, 221, .6);
  position: absolute;
  left: 17%;
  top: 10%;
  transition: 1.5s;
  opacity: 0;
  transform: translateX(-100%); }
.realizations__h3 {
  @include services__h3;
  color: #333D3C;
  margin-top: 2rem;
  margin-bottom: 1.4rem;
  &::after {
    content: "03";
    position: absolute;
    top: 14%;
    right: 32%;
    font-size: 1.1rem;
    color: #868782;
    font-weight: bold;
    letter-spacing: 1px; } }
.realizations__paragraph {
  @include services__paragraph;
  color: #868782;
  width: 30%;
  margin: 0 auto 3rem auto; }
.realizations__btn {
  @include services__btn;
  background-color: #BAB8AC;
  color: white; }
.services__offer {
  width: 29vw;
  height: 16vw;
  background-color: #86857D;
  position: absolute;
  right: 19%;
  bottom: 15%;
  transform: translateX(110%);
  opacity: 0;
  transition: 1.5s; }
.active {
  opacity: 1;
  transform: translateX(0); }
.offer__h3 {
  @include services__h3;
  color: white;
  margin-top: 1.5rem;
  margin-bottom: 1.4rem;
  padding-top: 2rem;
  &::after {
    content: "04";
    position: absolute;
    top: 45%;
    right: 38%;
    font-size: 1.1rem;
    color: #BAB8AC;
    font-weight: bold;
    letter-spacing: 1px; } }
.offer__paragraph {
  @include services__paragraph;
  color: white;
  width: 50%;
  margin: 0 auto 2rem auto; }
.offer__btn {
  @include services__btn;
  background-color: #CBF2F0;
  color: #333D3C; }
.object {
  position: absolute;
  &:nth-child(1) {
    background-image: url("../assets/img/servicesBgI/services2.png");
    width: 36px;
    height: 36px;
    bottom: 50%;
    left: 10%; }
  &:nth-child(2) {
    background-image: url("../assets/img/servicesBgI/services4.png");
    width: 101px;
    height: 87px;
    bottom: 35%;
    left: 25%; }
  &:nth-child(3) {
    background-image: url("../assets/img/servicesBgI/services1.png");
    width: 50px;
    height: 49px;
    top: 25%;
    right: 20%; }
  &:nth-child(4) {
    background-image: url("../assets/img/servicesBgI/services3.png");
    width: 19px;
    height: 19px;
    bottom: 25%;
    right: 10%; } }

