
.features {
  padding-top: 4rem;
  position: relative;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  opacity:  0;
  transform: scale(.5);
  transition: 1s; }
.active {
  transform: scale(1);
  opacity: 1; }
.features__h2 {
  text-align: center;
  color: #333d3c;
  font-weight: bold;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  text-transform: uppercase;
  font-size: 3.75rem;
  letter-spacing: 1.9rem;
  position: relative;
  margin-bottom: 0;
  &::after {
    content: "02";
    position: absolute;
    top: 15%;
    font-family: 'ff-good-headline-web-pro-ext', sans-serif;
    font-size: 1.2rem;
    color: #868782;
    font-weight: bold;
    letter-spacing: 1px; } }
.features__description {
  margin: 4rem auto;
  text-align: center;
  color: #868782;
  opacity: .9;
  font-size: 1rem;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  width: 33%;
  line-height: 2rem;
  font-weight: bold; }
.features__list {
  width: 100%;
  height: 150vh;
  list-style: none;
  align-items: center;
  padding: 0;
  position: relative;
  margin-top: 3rem;
  display: grid;
  grid-template-columns: auto;
  grid-template-rows: auto auto;
  grid-row-gap: 2rem; }
.feature__item {
  width: 21%;
  height: auto;
  margin: 0 auto;
  position: absolute;
  &:nth-of-type(1) {
    left: 34%;
    top: 1%; }
  &:nth-of-type(2) {
    right: 27%;
    top: 18%; }
  &:nth-of-type(3) {
    left: 20%;
    top: 35%; }
  &:nth-of-type(4) {
    right: 19%;
    top: 47%; }
  &:nth-of-type(5) {
    left: 28%;
    bottom: 22%; }
  &:nth-of-type(6) {
    right: 10%;
    bottom: 7%; } }
.feature__h3 {
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  font-size: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #333D3C;
  margin-bottom: 0; }
.feature__description {
  font-family: "sofia-pro", sans-serif;
  color: #868782;
