
.footer {
  width: 100%;
  position: relative;
  height: 95vh;
  overflow: hidden; }
.contact {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  padding: 0 5rem;
  width: 80%;
  margin: 0 auto; }

.contact__form {
  max-width: 18vw;
  height: auto;
  padding: 2rem 0 2rem 0;
  background-color: rgba(255,255,255, 0.3);
  display: grid;
  grid-row-gap: 1.5rem;
  justify-content: center;
  flex-grow: 1;
  overflow: hidden;
  opacity: 0;
  transition: 1.5s; }
.active {
  opacity: 1; }
.form__h4 {
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  font-size: 1.5rem;
  color: #333D3C;
  letter-spacing: 7px;
  margin: .5rem auto;
  text-transform: uppercase; }
.form__label {
  font-family: "sofia-pro",sans-serif;
  font-size: 0.75rem;
  color: rgba(51, 61, 60, .6);
  font-weight: bold;
  text-align: center;
  line-height: .5rem;
  padding: 2rem 0; }
.form__fields {
  display: flex;
  margin-bottom: 1rem;
  flex-direction: column; }
.form__input {
  border: none;
  background-color: transparent;
  border-bottom: 1px solid rgba(51, 61, 60, .4);
  padding: 0 4rem;
  text-align: center;
  font-family: "sofia-pro",sans-serif;
  font-size: 0.75rem;
  line-height: 1.7rem;
  color: rgba(51, 61, 60, .6);
  &::placeholder {
    color: rgba(51, 61, 60, .55);
    font-weight: bold; } }
.form__textarea {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid rgba(51, 61, 60, .4);
  resize: none;
  line-height: 1rem;
  text-align: center;
  color: rgba(51, 61, 60, .7);
  font-family: "sofia-pro",sans-serif;
  font-size: 0.75rem; }
.form__btn {
  font-size: 1.5rem;
  color: white;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  width: 4.7rem;
  background-color: #BAB8AC;
  border: none;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 2px;
  line-height: 1.7rem;
  margin: -1rem auto 0 auto; }
.contact__info {
  text-align: right;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 3.5rem 2rem; }
.info__h2 {
  font-size: 3rem;
  color: #333D3C;
  text-transform: uppercase;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  margin: 0;
  letter-spacing: 1px;
  &::after {
    content: "05";
    position: absolute;
    top: 2%;
    left: 40%;
    font-size: 1.2rem;
    color: #333D3C;
    font-weight: bold;
    letter-spacing: 1px; } }
.info__person {
  display: flex;
  flex-direction: column; }
.person__name {
  font-size: 1.5rem;
  text-transform: uppercase;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  color: #333D3C;
  letter-spacing: 1px;
  font-weight: bold;
  margin: .5rem 0; }
.person__email,.person__number {
  color: rgba(134, 135, 130, .85);
  font-size: 1rem;
  font-family: "sofia-pro", sans-serif; }
section.menu {
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 9vh;
  display: flex;
  justify-content: space-around;
  align-items: center; }
.menu__socials {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  width: 60px;
  padding: 0; }
.menu__item {
  height: 22px;
  width: 22px;
  opacity: .85; }
.menu__link {
  display: block; }
.menu__item--insta {
  background-image: url("../assets/img/footer/insta_big.png"); }
.menu__item--fb {
  background-image: url("../assets/img/footer/fb_big.png"); }
.nav__list {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
  align-self: center; }
.nav__item {
  padding: 0 2.5rem; }
.nav__link {
  text-decoration: none;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  font-size: 1.125rem;
  color: #333D3C;
  text-transform: uppercase;
  font-weight: bold;
  letter-spacing: 1px; }
.menu__logo--left {
