
.about {
  height: auto;
  margin-top: 3rem;
  position: relative;
  opacity: 0;
  transition: 3s; }
.active {
  opacity: 1; }
.about__grid {
  display: grid;
  grid-template-columns: 25% 25%;
  grid-template-rows: 1fr 1fr auto;
  justify-content: center;
  grid-row-gap: 2rem;
  grid-column-gap: 4rem; }
.grid__logo {
  grid-row-start: 1;
  grid-row-end: 1;
  grid-column-start: 1;
  grid-column-end: 1; }
.grid__link--after {
  position: relative;
  &::after {
    content: "01";
    position: absolute;
    top: 0;
    left: 70%;
    font-family: 'ff-good-headline-web-pro-ext', sans-serif;
    font-size: 1.2rem;
    color: #868782;
    font-weight: bold;
    letter-spacing: 1px; } }
.grid__link {
  margin-left: 2rem; }
.grid__paragraph {
  grid-row-start: 1;
  grid-row-end: 1;
  grid-column-start: 2;
  grid-column-end: 2;
  font-size: 0.875rem; }
.grid__logo2 {
  grid-row-start: 2;
  grid-row-end: 2;
  grid-column-start: 1;
  grid-column-end: 1;
  margin: 0 auto; }
.grid__paragraph-right {
  grid-row-start: 2;
  grid-row-end: 2;
  grid-column-start: 2;
  grid-column-end: 2; }
.grid__paragraph {
  font-family: "Sofia-pro", sans-serif;
  font-size: .875rem;
  font-weight: normal;
  line-height: 1.3rem;
  color: #868782; }
.paragraph__list {
  grid-row-start: 3;
  grid-row-end: 3;
  grid-column-start: 2;
  grid-column-end: 2;
  margin-top: -4rem;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  color: #868782;
  list-style: none;
  text-transform: uppercase;
  display: block;
  letter-spacing: 1px;
  padding: 0; }
.list__item {
  margin-bottom: .8rem;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  position: relative; }
.item__duty {
  font-size: 1rem;
  font-weight: bold;
  display: block;
  flex-shrink: 0;
  margin-right: .3rem; }
.item__separator {
  width: 100%;
  flex-shrink: 1;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    bottom: 0;
    transform: translateY(-200%);
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #333D3C; } }
.item__person {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333D3C;
  display: block;
  padding-right: 2rem;
  letter-spacing: 1.5px;
  flex-shrink: 0;
  line-height: 1.6rem;
  margin-left: .3rem;
  position: relative; }
.item__check-btn {
  background-color: rgba(0,0,0, .3);
  display: block;
  border: none;
  cursor: pointer;
  position: absolute;
  font-weight: bold;
  left: 95%;
  bottom: 0; }
.check-btn__span {
  color: white;
  text-transform: uppercase;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  font-size: 1.4rem;
  line-height: 1.3rem;
  letter-spacing: 1px;
  transition: font-size .2s;
  &:hover {
    font-size: 1.6rem; } }
.about__mission {
  background-color: #FFF2DD;
  width: 50%;
  height: auto;
  padding: 1.5rem 2rem 1rem 2rem;
  display: flex;
  justify-content: space-between;
  position: relative;
  margin: 6rem auto; }
.mission__paragraph {
  flex-basis: 50%;
  font-family: "sofia-pro", sans-serif;
  font-weight: normal;
  font-size: .875rem;
  color: #455251;
  opacity: .5;
  text-align: right;
  line-height: 1.07rem;
  border-right: 2px solid #333D3C;
  padding-right: 1rem; }
.mission__list {
  flex-basis: 50%;
  align-self: center;
  list-style: none; }
.mission__item {
  margin: 0 0 0 1rem; }
.item__link {
  font-family: 'ff-good-headline-web-pro-ext', sans-serif;
  text-decoration: none;
  font-weight: bold;
  color: #333D3C;
  font-size: 1.5rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  transition: font-size  .3s;
  &:hover {
    font-size: 2.5rem;
    letter-spacing: 3px; } }
.item__span {
  margin-right: .9rem; }
.mission__right-arrow {
  position: absolute;
  top: calc(50% - 16px);
  right: 0;
  margin-right: 2rem; }
.right-arrow {
  background-image: url("../assets/img/about/right-arrow.png");
  width: 32px;
  height: 32px;
  display: block;
  opacity: .3; }
.decoration {
  position: absolute;
  &:nth-child(1) {
    background-image: url("../assets/img/about/mid1.png");
    top: 23%;
    left: 41%;
    width: 20px;
    height: 20px; }
  &:nth-child(2) {
    background-image: url("../assets/img/about/mid2.png");
    top: 17%;
    left: 15%;
    width: 61px;
    height: 61px; }
  &:nth-child(3) {
    background-image: url("../assets/img/about/mid3.png");
    bottom: -10%;
    left: -3%;
    width: 252px;
    height: 315px; }
  &:nth-child(4) {
    background-image: url("../assets/img/about/mid4.png");
    bottom: -5%;
    right: 35%;
