.header {
  height: 100vh;
  width: 100%;
  padding: 3rem 3rem; }
.upper {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }
.upper__logo {
  transition: .2s;
  &:hover {
    transform: scale(1.2); } }
.upper__socials {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 50px;
  height: 17px;
  padding: 0;
  list-style-type: none; }
.socials__item {
  opacity: .7; }
.socials__link {
  width: 17px;
  height: 17px;
  display: block; }
.socials__item--insta {
  background: url("../assets/img/header/instagram.png"); }
.socials__item--fb {
  background: url("../assets/img/header/fb.png"); }
.upper__switches {
  font-family: 'ff-good-headline-web-pro-ext', sans-serif; }
.upper__switch {
  text-transform: uppercase;
  text-decoration: none;
  font-weight: bold;
  font-size: 1.125rem;
  letter-spacing: 1px;
  color: #333D3C;
  transition: .1s;
  &:nth-of-type(1) {
    margin-right: .5rem; } }
.upper__switch--js {
  border-bottom: 2px solid #333D3C; }
.header__container {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 4rem;
  position: relative;
  font-family: 'ff-good-headline-web-pro-ext', sans-serif; }
.header__paragraph {
  font-size: 1.5rem;
  text-transform: uppercase;
  color: #586967;
  letter-spacing: 2px;
  font-weight: normal;
  margin: 0 0 -.6rem 0; }
.header__h1 {
  font-size: 5rem;
  text-transform: uppercase;
  color: #333D3C;
  letter-spacing: 4px;
  font-weight: bold;
  margin: 2rem 0 1rem 0; }
.header__btn {
  color: white;
  border: 2px solid white;
  background-color: transparent;
  padding: .2rem 1rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  margin: 0;
  font-size: 1.5rem;
  cursor: pointer;
  transition: .2s linear;
  z-index: 2;
  &:hover {
    color: white;
    background-color: rgba(51, 61, 60, 0.7);
    border-color: transparent;
    transform: scale(1.1); } }
.header__anchor {
  display: block;
  margin-top: 10rem;
  text-transform: uppercase;
  color: #333D3C;
  font-weight: bold;
  letter-spacing: 1px;
  font-size: 1.125rem;
  text-decoration: none;
  &::before {
    content: "";
    background-image: url("../assets/img/header/span-tło.png");
    transform: scale(0.8);
    width: 769px;
    height: 636px;
    display: block;
    position: absolute;
    top: 0;
    left: calc(50% - 350px);
    z-index: -1; }
  &::after {
    content: "";
    background-image: url("../assets/img/header/cien.png");
    transform: scale(0.9);
    opacity: .5;
    width: 572px;
    height: 367px;
    display: block;
    position: absolute;
    bottom: -90%;
    left: calc(50% - 293px);
    z-index: -2; } }
.header__span {
  background-image: url("../assets/img/header/arrow-down.png");
  width: 32px;
  height: 32px;
  position: absolute;
  bottom: -35%;
  left: calc(50% - 16px);
  opacity: .7;
  transform: translateY(0);
  animation-name: pulse;
  animation-duration: 1s;
  animation-delay: 1s;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-timing-function: linear; }
.side-btn {
  padding: 5px 0 5px 5px;
  display: inline-block;
  cursor: pointer;
  background-color: transparent;
  border: 0;
  margin: 0;
  position: fixed;
  right: 0;
  top: 35%;
  z-index: 999; }
.side-btn__box {
  width: 50px;
  height: 36px;
  background-color: black;
  opacity: .7;
  display: inline-block;
  position: relative; }
.box__inner {
  width: 25px;
  height: 2px;
  background-color: white;
  position: absolute;
  left: calc(50% - 12.5px);
  &:nth-of-type(1) {
    top: 10px; }
  &:nth-of-type(2) {
    top: 50%;
    transform: translateY(-50%); }
  &:nth-of-type(3) {
    bottom:  10px; } }

@keyframes pulse {
  0% {
    transform: translateY(0); }
  50% {
    transform: translateY(65%); }
  100% {
    transform: translateY(0); } }

